<template>
  <div class='comment-list'>
    <h1></h1>
    <el-table :data="items">
      <el-table-column prop="_id" label="ID" width="240"></el-table-column>
      <el-table-column prop="user.username" label=""></el-table-column>
      <el-table-column prop="body" label=""></el-table-column>
      <el-table-column prop="clicks" label=""></el-table-column>
      <el-table-column prop="time" label=""></el-table-column>
      <el-table-column fixed="right" label=""  width="240">
      <template slot-scope="scope">
        <el-button type="primary" size="small" 
        @click="$router.push(`/comments/edit/${scope.row._id}`)"></el-button>
        <el-button type="danger" size="small" @click="remove(scope.row)"></el-button>
      </template>
    </el-table-column>
    </el-table>
  </div>
</template>

<script>
  import dayjs from 'dayjs'
  export default {
    data () {
      return {
        items: []
      }
    },
    methods: {
      async fetch(){
        const res = await this.$http.get('rest/comments')
        let that = this
        res.data.map(v => {    
          v.time = dayjs(v.updatedAt).format('YYYY/MM/DD HH:mm:ss')   
          that.items.push(v)           
        })
      },
      async remove(row){
        this.$confirm(`"${row.username}"`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(async () => {
          const res = await this.$http.delete(`rest/comments/${row._id}`)
          this.$message({
            type: 'success',
            message: '!'
          });
          this.items = [];
          this.fetch();
        })      
      }
    },
    created(){
      this.fetch()
    }
 }
</script>

